import { Component, OnInit, ViewChild } from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule } from '@angular/forms'
import { IonicModule } from '@ionic/angular'
import { ModalController } from '@ionic/angular/standalone'
import { IonRouterOutlet, LoadingController } from '@ionic/angular/standalone'
import {
  StripeElementsOptions,
  StripePaymentElementChangeEvent,
} from '@stripe/stripe-js'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import {
  StripeElementsDirective,
  StripePaymentElementComponent,
} from 'ngx-stripe'
import {
  fetchUserAttributes,
  FetchUserAttributesOutput,
} from 'aws-amplify/auth'
import { StripeService } from 'src/app/services/external/stripe.service'
import { ForfaitListModalComponent } from 'src/app/components/modals/forfaits/forfaits.component'
import { CguComponent } from 'src/app/components/modals/cgu/cgu.component'
import { ActivatedRoute, Router, RouterModule } from '@angular/router'
import { environment } from 'src/environments/environment'
import {
  ListForfait200ResponseAllOfDataInner,
  RetrieveProgram200Response,
} from 'src/api-cyclist'
import { CguService } from 'src/app/services/api-wrappers/cgu.service'
import { ForfaitsWrapperService } from 'src/app/services/api-wrappers/forfaits.service'
import { PaymentMethodsService } from 'src/app/services/api-wrappers/payment-method.service'
import { AccountService } from 'src/app/services/api-wrappers/account.service'
import { ToastService } from 'src/app/services/utils/toast.service'
import { SubscriptionProcessService } from 'src/app/services/utils/subscription-process.service'
import { firstValueFrom } from 'rxjs'
import { Capacitor } from '@capacitor/core'
import { StorageService } from 'src/app/services/utils/storage.service'
import { addIcons } from 'ionicons'
import { logoApple, logoGoogle } from 'ionicons/icons'
import { paymentMethodsState } from 'src/app/services/api-wrappers/payment-method.service'
import { effect } from '@angular/core'
import { KYCWrapperService } from 'src/app/services/api-wrappers/kyc.service'
import { ErrorHandlerService } from 'src/app/services/utils/error-handler.service'

@Component({
  selector: 'app-payment-method',
  templateUrl: './payment-method.page.html',
  styleUrls: ['./payment-method.page.scss'],
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    TranslateModule,
    StripeElementsDirective,
    StripePaymentElementComponent,
    HeaderComponent,
    RouterModule,
  ],
})
export class PaymentMethodPage implements OnInit {
  @ViewChild(StripePaymentElementComponent)
  paymentElement!: StripePaymentElementComponent
  layout: 'selecting' | 'confirming' | 'payment_method_added' = 'confirming'
  paymentMode:
    | 'classic'
    | 'classic_applepay'
    | 'classic_googlepay'
    | 'applepay'
    | 'googlepay' = 'classic'
  userAttributes!: FetchUserAttributesOutput
  options!: StripeElementsOptions
  defaultForfait: ListForfait200ResponseAllOfDataInner
  selectedForfait: ListForfait200ResponseAllOfDataInner
  cguAccepted: 'TRUE' | 'FALSE' = 'FALSE'
  stripeFormFilled: 'TRUE' | 'FALSE' = 'FALSE'
  skeletonCount: number = 3
  promoCode: string = ''
  cardsState: paymentMethodsState = { state: 'loading' }
  program: RetrieveProgram200Response | null = null
  hasForfaitsAvailable: 'false' | 'true' = 'false'
  constructor(
    public stripeService: StripeService,
    private modalCtrl: ModalController,
    private ionRouterOutlet: IonRouterOutlet,
    public router: Router,
    private cguService: CguService,
    private forfaitsService: ForfaitsWrapperService,
    private route: ActivatedRoute,
    public paymentMethodService: PaymentMethodsService,
    private accountService: AccountService,
    private loadingCtrl: LoadingController,
    private translateService: TranslateService,
    private toastr: ToastService,
    private subscriptionProcess: SubscriptionProcessService,
    private storage: StorageService,
    private kycService: KYCWrapperService,
    private errorHandler: ErrorHandlerService
  ) {
    addIcons({ logoApple, logoGoogle })
    effect(() => {
      this.cardsState = this.paymentMethodService.paymentMethodsState()
      if (
        this.cardsState.state === 'success' &&
        this.cardsState.paymentMethods.length > 0 &&
        this.layout !== 'confirming'
      ) {
        this.layout = 'payment_method_added'
      }
    })
  }

  async ngOnInit() {
    // Check if external user - redirect them away from payment method page
    const cyclistState = this.accountService.cyclistState()
    if (
      'cyclist' in cyclistState &&
      (cyclistState.cyclist.role == 'guest' ||
        cyclistState.cyclist.registrations?.data?.[0]?.role == 'guest')
    ) {
      this.router.navigate(['/verify-phone'], { replaceUrl: true })
      return
    }

    this.program = await this.storage.get('program')
    //AFTER CONFIRM SETUP, CHECK THE ROUTE PARAMS AND IF ONES, WE SET THE DEFAULT CARD
    const urlParams = this.route.snapshot.queryParams
    const setupIntent = urlParams['setup_intent']
    const clientSecret = urlParams['setup_intent_client_secret']
    const redirectStatus = urlParams['redirect_status']
    if (setupIntent && clientSecret && redirectStatus === 'succeeded') {
      this.layout = 'confirming'
      const loading = await this.loadingCtrl.create({
        message: this.translateService.instant('generic.confirming'),
      })
      try {
        await loading.present()
        await this.accountService.loadCyclist()
        await this.setupPaymentMethod(clientSecret)
        await this.validateTermsAndRedirect()
      } finally {
        await loading.dismiss()
      }
    } else {
      this.initPageData()
    }
  }

  async initPageData() {
    this.layout = 'selecting'
    fetchUserAttributes().then((attributes) => {
      this.userAttributes = attributes
    })

    // Initialize Stripe Elements in any case as we'll need the clientSecret
    this.stripeService
      .initStripeElements(environment.program)
      .subscribe(async (options) => {
        this.options = options

        if (Capacitor.getPlatform() === 'ios') {
          const isAvailable = await this.stripeService.isApplePayAvailable()
          this.paymentMode = isAvailable ? 'applepay' : 'classic'
        }
        if (Capacitor.getPlatform() === 'android') {
          const isAvailable = await this.stripeService.isGooglePayAvailable()
          this.paymentMode = isAvailable ? 'googlepay' : 'classic'
        }
      })

    this.forfaitsService.getDefaultForfait().then((forfait) => {
      if (forfait) {
        this.defaultForfait = forfait
        this.selectedForfait = forfait
      }
    })

    this.hasForfaitsAvailable = (await this.forfaitsService.hasForfaits())
      ? 'true'
      : 'false'
  }

  async openCguModal(event: MouseEvent) {
    event.stopPropagation()
    const modal = await this.modalCtrl.create({
      component: CguComponent,
      presentingElement: this.ionRouterOutlet.nativeEl,
    })
    await modal.present()
  }

  async openForfaitModal(event: MouseEvent) {
    event.stopPropagation()
    const modal = await this.modalCtrl.create({
      component: ForfaitListModalComponent,
      componentProps: {
        next: 'onboarding',
      },
      presentingElement: this.ionRouterOutlet.nativeEl,
    })
    modal.onDidDismiss().then(async (result) => {
      if (result.data) {
        this.promoCode = ''
        this.selectedForfait = result.data.forfait
        await this.subscriptionProcess.startProcess(this.selectedForfait)
        await this.subscriptionProcess.updateStep('payment')
      }
    })
    await modal.present()
  }

  async setupPaymentMethod(clientSecret: string): Promise<boolean> {
    return new Promise<boolean>((resolve) => {
      this.stripeService.retrieveSetupIntent(clientSecret).subscribe({
        next: async (result) => {
          if (
            result &&
            result.setupIntent &&
            result.setupIntent.payment_method
          ) {
            await this.paymentMethodService.setDefaultCard(
              result.setupIntent.payment_method as string
            )
            if (this.layout !== 'confirming') {
              await this.accountService.loadCyclist()
              await this.paymentMethodService.getPaymentMethods()
              this.layout = 'payment_method_added'
            }
            resolve(true)
          } else {
            resolve(false)
          }
        },
        error: () => {
          this.layout = 'selecting'
          resolve(false)
        },
      })
    })
  }

  async confirmSetup() {
    const loading = await this.loadingCtrl.create({
      message: this.translateService.instant('generic.confirming'),
    })

    await loading.present()

    this.stripeService
      .confirmSetup(
        this.paymentElement.elements,
        this.options.clientSecret!,
        this.userAttributes.name!
      )
      .subscribe({
        next: async () => {
          await loading.dismiss()
        },
        error: async () => {
          this.layout = 'selecting'
          await loading.dismiss()
        },
      })
  }

  async openApplePay() {
    const loading = await this.loadingCtrl.create({
      message: this.translateService.instant('generic.confirming'),
    })

    try {
      const result = await this.stripeService.setupApplePay(
        this.options.clientSecret!,
        this.program?.name || '',
        this.program?.config?.applepay_merchant || ''
      )

      if (result === 'completed') {
        await loading.present()
        await this.setupPaymentMethod(this.options.clientSecret!)
        await loading.dismiss()
      } else if (result === 'failed') {
        this.layout = 'selecting'
      } else {
        this.layout = 'selecting'
      }
    } catch (error) {
      this.layout = 'selecting'
    }
  }

  async openGooglePay() {
    const loading = await this.loadingCtrl.create({
      message: this.translateService.instant('generic.confirming'),
    })

    try {
      const result = await this.stripeService.setupGooglePay(
        this.options.clientSecret!,
        this.program?.name || 'Ecovelo'
      )

      if (result === 'completed') {
        await loading.present()
        await this.setupPaymentMethod(this.options.clientSecret!)
        await loading.dismiss()
      } else if (result === 'failed') {
        this.layout = 'selecting'
        this.paymentMode = 'classic'
      } else {
        this.layout = 'selecting'
        this.paymentMode = 'classic'
      }
    } catch (error) {
      this.layout = 'selecting'
      this.paymentMode = 'classic'
    }
  }

  async validateTermsAndRedirect() {
    await this.cguService.validateTerms()
    const process = await firstValueFrom(
      this.subscriptionProcess.getCurrentProcess()
    )

    const hasKycDocuments =
      this.kycService.enabledDocumentTypes() &&
      this.kycService.enabledDocumentTypes().length > 0

    const hasSupportingDocuments =
      process?.forfait?.supporting_documents &&
      process?.forfait?.supporting_documents?.length > 0

    if (hasKycDocuments) {
      // KYC comes first if required
      this.router.navigate(['/kyc'], {
        queryParams: {
          onboarding: 'true',
        },
        replaceUrl: true,
      })
    } else if (hasSupportingDocuments) {
      // Supporting documents if no KYC
      await this.subscriptionProcess.updateStep('documents')
      this.router.navigate(['/supporting-documents'], {
        queryParams: { next: '/verify-phone' },
        replaceUrl: true,
      })
    } else {
      // Direct to phone verification if no documents needed
      await this.subscriptionProcess.updateStep('phone')
      this.router.navigate(['/verify-phone'])
    }
  }

  async onPromoCodeChange() {
    await this.subscriptionProcess.updateReduction(this.promoCode)
  }

  getIcon(brand: string | undefined): string {
    switch (brand) {
      case 'visa':
        return '../../../assets/svg/fontawesome/visa.svg'
      case 'mastercard':
        return '../../../assets/svg/fontawesome/mastercard.svg'
      default:
        return '../../../assets/svg/fontawesome/credit-card.svg'
    }
  }

  async cancelSelection() {
    this.selectedForfait = this.defaultForfait
    await this.subscriptionProcess.clearProcess()
    this.promoCode = ''
  }
  toggleStripeFormFilled(event: StripePaymentElementChangeEvent) {
    if (
      event.value?.type === 'apple_pay' &&
      this.paymentMode !== 'classic_applepay'
    ) {
      this.paymentMode = 'classic_applepay'
    } else if (
      event.value?.type === 'google_pay' &&
      this.paymentMode !== 'classic_googlepay'
    ) {
      this.paymentMode = 'classic_googlepay'
    } else if (!event.value?.type && this.paymentMode !== 'classic') {
      this.paymentMode = 'classic'
    }
    this.stripeFormFilled = event.complete ? 'TRUE' : 'FALSE'
  }
  toggleCgu(event: CustomEvent) {
    switch (event.detail.checked) {
      case true:
        this.cguAccepted = 'TRUE'
        break
      case false:
        this.cguAccepted = 'FALSE'
        break
      default:
        this.cguAccepted = 'FALSE'
        break
    }
  }
}
